
import { defineComponent, ref } from 'vue'
import useServer from '@/hooks/useServer'
export default defineComponent({
  name: 'title-dialog',
  emits: ['close'],
  setup(props, { emit }) {
    
    const { saveJSON, getTitle } = useServer()
    const title = ref('')
    title.value = getTitle() || ''
    const close = () => emit('close')

    const save = () => {
      if (title.value) {
        saveJSON(title.value)
        close()
      }
    }

    return {
      title,
      close,
      save,
    }
  },
})
